import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    infoData: [],
    foodData: []
  },
  mutations: {
    // toggleInfoShow (state) {
    //   state.infoDetailShow = !state.infoDetailShow
    // },
    addInfoData (state, params) {
      state.infoData.push(params)
    },
    clearInfoData (state) {
      state.infoData = []
    },
    addFoodData (state, params) {
      state.foodData.push(params)
    },
    clearFoodData (state) {
      state.foodData = []
    }
  },
  actions: {
  },
  modules: {
  }
})
