import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'style/reset.css' // 初始化样式
import 'style/icon/iconfont.css' // 字体图标
import VueLazyload from 'vue-lazyload'
// Vue.prototype.baseUrl = 'http://localhost:3000'
Vue.prototype.baseUrl = 'http://123.56.100.146'
// const url = 'http://localhost:3000/public/img/logo.png'
const url = 'http://123.56.100.146/public/img/logo.png'
Vue.use(VueLazyload, {
  preLoad: 1.3,
  error: url,
  loading: url,
  attempt: 1
})
Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
