<template>
  <div class="infoDetail" v-if="infoData.length">
    <!-- 头部 -->
    <div class="title">
      <i class="iconfont" @click="clearInfoData">&#xe6b6;</i>
      <h1>详情</h1>
    </div>
    <!-- 滚动列表 -->
    <div class="wrapper" ref="wrapper">
      <div class="content">
        <h2 @click="toggleTitleShow" :class="titleShowAll?'':'sl'">{{list.title}}</h2>
        <div class="user">
          <img :src="list.avator" alt="">
          <div class="text">
            <h5>{{list.userName}}</h5>
            <span>{{list.time}}</span>
          </div>
        </div>
        <div class="infos">
          <p :class="showAll?'':'es3'" @click="toggleEs">{{list.content.text}}</p>
          <div class="imgs">
            <img :src="item" alt=""
              v-for="(item, index) in list.content.imgs" :key="index"
            >
          </div>
        </div>
      </div>
    </div>
    <!-- 底部 -->
    <footer>
      <div class="btn">
        <i class="iconfont">&#xe67d;</i><span>{{list.look}}</span>
      </div>
      <div class="btn">
        <i class="iconfont">&#xe7ce;</i><span>{{list.collect}}</span>
      </div>
      <div class="btn">
        <i class="iconfont">&#xe60f;</i><span>{{list.share}}</span>
      </div>
    </footer>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
import Bs from 'better-scroll'
import { handleDataIssue } from 'utils/handleData'
export default {
  data () {
    return {
      showAll: false,
      titleShowAll: false,
      list: []
    }
  },
  computed: {
    ...mapState(['infoData'])
  },
  methods: {
    ...mapMutations(['clearInfoData']),
    initBs () {
      const wrapper = this.$refs.wrapper
      /* eslint-disable no-new */
      this.Bs = new Bs(wrapper, { click: true })
    },
    // 切换多行省略
    toggleEs () {
      this.showAll = !this.showAll
    },
    toggleTitleShow () {
      this.titleShowAll = !this.titleShowAll
    }
  },
  watch: {
    infoData () {
      if (this.infoData.length === 0) { return false }
      const data = handleDataIssue(this.infoData[0])
      this.list = data
      this.$nextTick(() => {
        this.initBs()
      })
    }
  }
}
</script>
<style lang="less" scoped>
@import '~style/index.less';
.infoDetail{
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  z-index: 1;
  background: #fff;
  .title{
    width: 6.9rem;
    margin: auto;
    line-height: .88rem;
    text-align: center;
    position: relative;
    color: #222;
    h1{
      font-size: .34rem;
    }
    .iconfont{
      font-size: .34rem;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  .wrapper{
    overflow: hidden;
    position: fixed;
    top: .88rem;
    bottom: 1rem;
    left: 0;
    right: 0;
    .content{
      width: 6.9rem;
      padding: 0 .3rem;
      h2{
        width: 6rem;
        font-size: .42rem;
        line-height: 0.44rem;
        color: #000000;
        margin: .3rem 0;
      }
      .user{
        height: 0.9rem;
        margin-bottom: .3rem;
        display: flex;
        img{
          width: 0.9rem;
          height: 0.9rem;
          border-radius: 50%;
          margin-right: 0.3rem;
        }
        .text{
          flex: 1;
          h5{
            font-size: .28rem;
            line-height: 0.48rem;
          }
          span{
            font-size: .20rem;
            line-height: .32rem;
            color: #999999;
          }
        }
      }
      .infos{
        p{
          width: 100%;
          font-size: .26rem;
          line-height: 0.44rem;
          color: #555555;
          text-align: justify;
        }
        .imgs{
          display: flex;
          flex-wrap: wrap;
          justify-content: space-between;
          img{
            width: 100%;
            border-radius: .18rem;
            margin: .2rem 0;
          }
          .onlyOne{
            width: 100%;
          }
        }
      }
    }
  }
  footer{
    height: 1rem;
    display: flex;
    border-top: 1px solid #dededf;
    box-sizing: border-box;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    .btn{
      font-size: .3rem;
      color: #777777;
      .iconfont{
        font-size: .3rem;
        margin-right: .13rem;
      }
    }
  }
}
</style>
