// 循环数据 按指定字段分组数据 返回数组
const cycleData = (kw, data, contentArray) => {
  const obj = {}
  for (let index = 0; index < data.length; index++) {
    let content = data[index] // 默认获取所有数据
    if (contentArray) { // 传入contentArray 只获取contentArray 里的数据
      content = {}
      for (let i = 0; i < contentArray.length; i++) {
        const key = contentArray[i]
        content[key] = data[index][key]
      }
    }
    const item = data[index][kw]
    if (obj[item]) {
      obj[item].push(content)
    } else {
      obj[item] = [content]
    }
  }
  return obj
}
const handleData = (data, contentArray) => {
  // 按系列分组数据
  const seriesObj = cycleData('series', data, contentArray)
  const seriesArray = []
  for (const key in seriesObj) {
    seriesArray.push({ seriesName: key, list: seriesObj[key] })
  }
  // 按分类分组数据
  for (let index = 0; index < seriesArray.length; index++) {
    const data = seriesArray[index].list
    const classifyObj = cycleData('classify', data)
    const classifyArray = []
    for (const key in classifyObj) {
      classifyArray.push({ classifyName: key, list: classifyObj[key] })
    }
    seriesArray[index].list = classifyArray
  }
  return seriesArray
}
// 处理菜品数据
const handleDataFood = (data, baseUrl) => {
  for (let index = 0; index < data.length; index++) {
    // 拼接图片路径
    data[index].img = baseUrl + data[index].img
    const step = data[index].step
    for (let i = 0; i < step.length; i++) {
      const src = baseUrl + step[i].img
      step[i].img = src
    }
  }
  return data
}
// 处理动态数据
const handleDataIssue = (data, baseUrl) => {
  const nowTime = (new Date()).getTime()
  for (let index = 0; index < data.length; index++) {
    // 拼接图片路径
    data[index].avator = baseUrl + data[index].avator
    const imgs = data[index].content.imgs
    for (let i = 0; i < imgs.length; i++) {
      const src = baseUrl + imgs[i]
      imgs[i] = src
    }
    const time = nowTime - data[index].time
    // 年
    const year = parseInt(time / (60 * 60 * 1000 * 24 * 30 * 365))
    if (year !== 0) { data[index].time = year + '年前'; continue }
    // 月
    const month = parseInt(time / (60 * 60 * 1000 * 24 * 30))
    if (month !== 0) { data[index].time = month + '月前'; continue }
    // 天
    const date = parseInt(time / (60 * 60 * 1000 * 24))
    if (date !== 0) { data[index].time = date + '天前'; continue }
    // 小时
    const hours = parseInt(time / (1000 * 60 * 60))
    if (hours !== 0) { data[index].time = hours + '小时前'; continue }
    // 分钟
    const minutes = parseInt(time / (1000 * 60))
    if (minutes !== 0) { data[index].time = minutes + '分钟前'; continue }
    // 秒
    const seconds = parseInt(time / 1000)
    if (seconds !== 0) { data[index].time = seconds + '秒前' } else {
      data[index].time = '刚刚'
    }
  }
  return data
}
// 将用户信息存到localStorage
const modifyLocalStorage = (state, data) => {
  console.log(data)
  let userInfo = {}
  userInfo.state = state
  userInfo.userName = data.userName
  userInfo.passWord = data.passWord
  if (data) {
    userInfo.userId = data._id
    userInfo.avator = data.avator
    userInfo.intro = data.intro
    userInfo.grade = data.grade
    userInfo.email = data.email
    userInfo.gender = data.gender
  }
  userInfo = JSON.stringify(userInfo)
  localStorage.setItem('userInfo', userInfo)
}
export {
  cycleData,
  handleData,
  handleDataIssue,
  handleDataFood,
  modifyLocalStorage
}
