<template>
  <div class="foodDetail" v-if="foodData.length">
    <!-- 头部 -->
    <div class="title">
      <i class="iconfont" @click="clearFoodData">&#xe6b6;</i>
      <h1>每日美食</h1>
    </div>
    <div class="wrapper" ref="wrapper">
      <div class="content">
        <div class="banner" :style="{'background':`url(${list.img})`}"></div>
        <div class="detail">
          <div class="name">
            <h2>{{list.foodName}}</h2>
            <div class="state">
              <p><i class="iconfont">&#xe67d;</i><span>{{list.look}}</span></p>
              <p><i class="iconfont">&#xe7ce;</i><span>{{list.collect}}</span></p>
              <p><i class="iconfont">&#xe60f;</i><span>{{list.share}}</span></p>
            </div>
          </div>
          <!-- 用料 -->
          <div class="material">
            <h3><span>{{list.foodName}}</span>的用料</h3>
            <div class="box">
              <p v-for="(item, index) in list.material" :key="index">
                <span class="name">{{item.name}}</span><span class="dose">{{item.dose}}</span>
              </p>
            </div>
          </div>
          <!-- 步骤 -->
          <div class="step">
            <h3><span>{{list.foodName}}</span>的做法</h3>
            <div class="box" v-for="(item, index) in list.step" :key="index">
              <h6><span>{{index+1}}</span><span>/{{list.step.length}}</span></h6>
              <img :src="item.img" alt="">
              <p>{{item.text}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import { mapState, mapMutations } from 'vuex'
import Bs from 'better-scroll'
export default {
  data () {
    return {
      list: []
    }
  },
  computed: {
    ...mapState(['foodData'])
  },
  methods: {
    ...mapMutations(['clearFoodData']),
    initBs () {
      const wrapper = this.$refs.wrapper
      /* eslint-disable no-new */
      this.Bs = new Bs(wrapper, { click: true })
    }
  },
  watch: {
    foodData () {
      if (!this.foodData.length) { return false }
      this.list = this.foodData[0]
      this.$nextTick(() => {
        this.initBs()
      })
    }
  }
}
</script>
<style lang="less" scoped>
@import '~style/index.less';
.foodDetail{
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: 1;
  background: #ffffff;
  .title{
    width: 6.9rem;
    margin: auto;
    line-height: .88rem;
    text-align: center;
    position: relative;
    color: #222;
    h1{
      font-size: .34rem;
    }
    .iconfont{
      font-size: .34rem;
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  .wrapper{
    overflow: hidden;
    position: fixed;
    top: .88rem;
    bottom: 0;
    left: 0;
    right: 0;
    .content{
      .banner{
        width: 100%;
        height: 5rem;
      }
      .detail{
        width: 6.9rem;
        padding: .3rem;
      }
      .name{
         h2{
          margin: .2rem auto;
          font-size: .38rem;
          line-height: 0.46rem;
          color: #333333;
          text-align: center;
        }
        .state{
          display: flex;
          justify-content: center;
          align-items: center;
          p{
            font-size: .24rem;
            line-height: 0.3;
            margin: 0 .1rem;
            color: @gray;
            .iconfont{
              font-size: .24rem;
              margin-right: .05rem;
            }
          }
        }
      }
      .material{
        margin-top: .4rem;
        h3{
          font-size: .3rem;
          line-height: .4rem;
        }
        .box{
          display: flex;
          flex-wrap: wrap;
          margin: .2rem 0;
          p{
            margin-right: .3rem;
            .name{
              color: @orange;
              margin-right: .1rem;
            }
          }
        }
      }
      .step{
        margin: .6rem auto .4rem;
        h3{
          font-size: .3rem;
          line-height: .4rem;
          margin: .2rem 0;
        }
        .box{
          margin: .2rem 0;
          p{
            font-size: .26rem;
            line-height: .4rem;
          }
          img{
            width: 100%;
            height: 3.6rem;
            border-radius: .2rem;
            margin: .1rem 0;
          }
        }
      }
    }
  }
}
</style>
