import Vue from 'vue'
import VueRouter from 'vue-router'
const IndexPage = () => import('components/IndexPage')
const Series = () => import('components/Series')
const Community = () => import('components/Community')
const My = () => import('components/My')
const GroupPage = () => import('components/GroupPage')
const MyIssue = () => import('components/MyIssue')
const UserInfo = () => import('components/UserInfo')
const LoginReg = () => import('components/LoginReg')
const IssueInfos = () => import('components/IssueInfos')

Vue.use(VueRouter)

const routes = [
  {
    path: '/indexPage', // 首页
    component: IndexPage,
    children: [
      {
        path: 'groupPage', // 分组页
        component: GroupPage
      }
    ]
  },
  {
    path: '/Series', // 全部分类
    component: Series
  },
  {
    path: '/community', // 社区
    component: Community,
    children: [
      {
        path: 'issueInfos', // 发布动态
        component: IssueInfos
      }
    ]
  },
  {
    path: '/my', // 我的
    component: My,
    children: [
      {
        path: 'myIssue/:state', // 我的发布
        component: MyIssue
      },
      {
        path: 'userInfo', // 个人信息
        component: UserInfo
      },
      {
        path: 'loginReg', // 登录
        component: LoginReg
      }
    ]
  },
  {
    path: '/', // 路由重定向
    redirect: 'indexPage'
  }
]

const router = new VueRouter({
  routes
})

export default router
